<template>
    <div id="categoryComponent2" class="pt-md">
        <ul class="component-list">
          <li>- moveItem(val, category) :</li>
          <li> -   </li>
          <li> - Val : , , ,   </li>
          <li> - Category: , ,    </li>
          <li> - Api  </li>
        </ul>
    </div>
    <div v-if="editor" class="editor">
        <editor-content :editor="editor" />
    </div>
</template>
  
  <script>
  import CodeBlockLowlight from '@tiptap/extension-code-block-lowlight'
  import Document from '@tiptap/extension-document'
  import Paragraph from '@tiptap/extension-paragraph'
  import Text from '@tiptap/extension-text'
  import { Editor, EditorContent } from '@tiptap/vue-3'
  import css from 'highlight.js/lib/languages/css'
  import js from 'highlight.js/lib/languages/javascript'
  import ts from 'highlight.js/lib/languages/typescript'
  import html from 'highlight.js/lib/languages/xml'
  import { lowlight } from 'lowlight'
  
  lowlight.registerLanguage('html', html)
  lowlight.registerLanguage('css', css)
  lowlight.registerLanguage('js', js)
  lowlight.registerLanguage('ts', ts)
  
  export default {
    components: {
      EditorContent,
    },
  
    data() {
      return {
        editor: null,
      }
    },
  
    mounted() {
      this.editor = new Editor({
        extensions: [
          Document,
          Paragraph,
          Text,
          CodeBlockLowlight.configure({
            lowlight,
          }),
        ],
        content: `<pre><code class="language-javascript">
    async moveItem(val, category) {
      this.errorMsg = ''
      let list = [] //  
      let model = null //   value
      const moveList = []

      // switch   
      switch (category) {
        case 1:
          list = this.categoryBigList
          model = this.categoryBig
          break
          .
          .
        default:
          list = []
          model = null
      }
      if (model === -1) {
        this.errorMsg = '     .'
        this.showNotifi()
      } else {
        const idx = list.findIndex((x) => x.auto_number === model)
        const up = idx - 1
        const down = idx + 1
        const move = list.splice(idx, 1)
        moveList.push(move[0])
        for (let i = 0; i < moveList.length; i += 1) {
          if (val === 1) list.splice(0, 0, moveList[i]) // 
          if (val === 2) list.splice(up, 0, moveList[i]) // 
          if (val === 3) list.splice(down, 0, moveList[i]) // 
          if (val === 4) list.splice(list.length, 0, moveList[i]) // 
        }
        //    
        if (category === 1) await categoryAPI.modifyCagetoryBigList({ list })
        else if (category === 2) await categoryAPI.modifyCagetoryMiddleList({ list })
        else if (category === 3) await categoryAPI.modifyCagetorySmallList({ list })
      }
    },
</code>
</pre>
        `,
        editable: false 
      })
    },
  
    beforeUnmount() {
      this.editor.destroy()
    },
  }
  </script>
  